import React from 'react';
import axios from 'axios';
import { Grid } from '@material-ui/core';
import './App.css';
import { CURRENCIES } from "./constants";
import GoodForm from './components/goodForm';
import GoodList from './components/goodList';

export default class App extends React.Component {

  state = {
    goods: [{
      name: 'Очень нужный товар0',
      quantity: 3,
      currency: CURRENCIES.USD,
      price: 16.5,
    },
      {
      name: 'Очень нужный товар1',
      quantity: 2,
      currency: CURRENCIES.USD,
      price: 11.4,
    },
      {
      name: 'Очень нужный товар2',
      quantity: 4,
      currency: CURRENCIES.RUB,
      price: 13.3,
    },
      {
        name: 'Очень нужный товар3',
        quantity: 8,
        currency: CURRENCIES.EUR,
        price: 5.5,
      }],
    currentGood: {
      name: '',
      price: 0,
      quantity: 0,
      currency: CURRENCIES.RUB,
    },
    sum: '',
  }

  addGood = () => {
    const { goods, currentGood } = this.state;
    goods.push(currentGood);

    this.setState({ goods, currentGood: {
        name: '',
        price: 0,
        quantity: 0,
        currency: CURRENCIES.RUB,
      } });
  }

  removeGood = (id) => (event) => {
    const { goods } = this.state;
    goods.splice(id, 1);
    this.setState({ goods });
  }

  onChange = (field) => (event) => {
    const { currentGood } = this.state;
    currentGood[field] = event.target.value;
    this.setState({ currentGood });
  }

  calculateSum = async () => {
    const { goods } = this.state;
    try {
      const { data } = await axios.post('http://localhost:3000/good/calculate', { goods });
      this.setState({ sum: data });
    } catch (err) {
      const { error } = err.response.data;
      alert(error);
    }
  }

  render() {
    const { goods, currentGood, sum } = this.state;
    return (
      <Grid container>
        <Grid item xs={8}>
          <GoodList goods={goods} sum={sum} removeGood={this.removeGood} calculateSum={this.calculateSum}/>
        </Grid>
        <Grid item xs={4}>
          <GoodForm currentGood={currentGood} addGood={this.addGood} onChange={this.onChange}/>
        </Grid>
      </Grid>
    );
  }
}
