import React from 'react';
import {
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Typography,
} from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import GoodItem from './goodItem';
import ShowSum from "./showSum";

const styles = {
  paper: {
    padding: 10,
    margin: 10,
  },
  items: {
    margin: 4,
  },
};

class GoodList extends React.Component {
  render() {
    const {calculateSum, classes, goods, sum, removeGood, error} = this.props;
    return (
      <Paper className={classes.paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>
                № п/п
              </TableCell>
              <TableCell>
                Название
              </TableCell>
              <TableCell>
                Количество
              </TableCell>
              <TableCell>
                Цена за ед
              </TableCell>
              <TableCell>
                Валюта
              </TableCell>
              <TableCell>
                 Удалить
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {
              goods.map((good, id) => (<GoodItem good={good} removeGood={removeGood} id={id} key={id}/>))
            }
          </TableBody>
        </Table>
        {
          sum && <ShowSum sum={sum}/>
        }
        {
          error && <Typography>{error}</Typography>
        }
        <Button onClick={calculateSum} variant='contained' color='primary'>
          Рассчитать общую стоимость
        </Button>
      </Paper>
    )
  }
}

export default withStyles(styles)(GoodList);
