import React from 'react';
import { Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

const styles = {
  paper: {
    padding: 10,
  },
  items: {
    margin: 4,
  },
};

class ShowSum extends React.Component {
  render() {
    const {sum} = this.props;
    const { RUB, EUR, USD } = sum;
    return (
      <React.Fragment>
        <Typography>
          Сумма товаров в RUB: {RUB}
        </Typography>
        <Typography>
          Сумма товаров в USD: {USD}
        </Typography>
        <Typography>
          Сумма товаров в EUR: {EUR}
        </Typography>
      </React.Fragment>
    )
  }
}

export default withStyles(styles)(ShowSum);
