import React from 'react';
import { IconButton, TableCell, TableRow } from '@material-ui/core';
import DeleteIcon from '@material-ui/icons/Delete';
import { withStyles } from '@material-ui/core/styles';

const styles = {
  paper: {
    padding: 10,
  },
  items: {
    margin: 4,
  },
};

class GoodItem extends React.Component {
  render() {
    const {good, id, removeGood } = this.props;
    const { name, quantity, currency, price } = good;
    return (
      <TableRow>
        <TableCell>
          {id + 1}
        </TableCell>
        <TableCell>
          {name}
        </TableCell>
        <TableCell>
          {quantity}
        </TableCell>
        <TableCell>
          {price}
        </TableCell>
        <TableCell>
          {currency}
        </TableCell>
        <TableCell>
          <IconButton onClick={removeGood(id)} color='secondary'>
            <DeleteIcon />
          </IconButton>
        </TableCell>
      </TableRow>
    )
  }
}

export default withStyles(styles)(GoodItem);
