import React from 'react';
import { Button, MenuItem, Paper, Select, TextField, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';

import {CURRENCIES} from "../constants";

const styles = {
  paper: {
    padding: 10,
    margin: 10,
  },
  items: {
    margin: 4,
  },
};

class GoodForm extends React.Component {
  render() {
    const {classes, addGood, onChange, currentGood} = this.props;
    const { name, quantity, currency, price } = currentGood;
    const currencies = Object.keys(CURRENCIES);
    return (
      <Paper className={classes.paper}>
        <TextField value={name} onChange={onChange('name')} name='name' label='Название товара' variant='outlined' fullWidth className={classes.items}></TextField>
        <TextField value={quantity} onChange={onChange('quantity')} name='quantity' label='Количество' variant='outlined' fullWidth className={classes.items}></TextField>
        <Typography className={classes.items}>Валюта:</Typography>
        <Select value={currency} onChange={onChange('currency')} name='currency' className={classes.items}>
          {
            currencies.map((currency, index) => (<MenuItem key={index} value={CURRENCIES[currency]}>{CURRENCIES[currency]}</MenuItem>))
          }
        </Select>
        <TextField value={price} onChange={onChange('price')} name='price' label='Цена' variant='outlined' fullWidth className={classes.items}></TextField>
        <Button onClick={addGood} className={classes.items} variant='contained' color='primary' fullWidth>Добавить товар</Button>
      </Paper>
    )
  }
}

export default withStyles(styles)(GoodForm);
